<template>
    <div v-if="!ring" :class="classes" class="progress" :style="styles"></div>

    <div v-if="ring" :class="classes" class="circle">
        <svg viewBox="0 0 160 160">
            <g stroke-width="20">
                <g class="background">
                    <circle cx="80" cy="80" r="80" stroke="none" />
                    <circle cx="80" cy="80" r="70" fill="none" />
                </g>
                <g class="progressbar" :stroke-dashoffset="percentageOfRing">
                    <circle cx="80" cy="80" r="80" stroke="none" />
                    <circle cx="80" cy="80" r="70" fill="none" />
                </g>
            </g>
        </svg>
    </div>
</template>

<script>
export default {
    name: 'j-progress',
    props: {
        value: {
            type: [Number, String],
            default: 0,
        },
        failed: {
            type: Boolean,
            default: false,
        },
        pause: {
            type: Boolean,
            default: false,
        },
        height: {
            type: String,
            default: '8px',
        },
        ring: {
            type: Boolean,
            default: false,
        },
    },
    computed: {
        // ,100
        percentage: function () {
            return Math.abs(this.value) > 100 ? 100 : Math.abs(this.value);
        },
        percentageOfRing() {
            return 440 - 4.4 * this.percentage;
        },
        styles: function () {
            let style = {};
            style['--percentage'] = this.percentage + '%';
            style['--height'] = this.height;
            return style;
        },
        classes: function () {
            let className = [];

            // 
            className.push(this.percentage == 100 ? 'complete' : '');
            className.push(this.failed ? 'failed' : '');
            className.push(this.pause ? 'pause' : '');

            return className;
        },
    },
};
</script>

<style>
.progress {
    --background-color: #bdc3c7;
    --c1: #5fc2ff;
    --c2: #3498db;
    --failed: #e74c3c;
    --complete: #2ecc71;
    /* --height: 8px; */
}

/*  */
.smal {
    --height: 4px;
}

.progress.pause {
    --c1: #ffca58;
    --c2: #ecb02c;
}

/*  */
.progress::before,
.progress::after,
.progress {
    height: var(--height);
    border-radius: calc(var(--height) / 2);
}

/*  */

.circle .progressbar,
.progress::before,
.progress::after {
    content: '';
    display: block;
    transition: 0.5s cubic-bezier(0, 1, 1, 1);
}

/* ,, */
.progress::after {
    transform: translateY(calc(var(--height) * -1));
}

/*  */
.progress {
    margin: 5px 0;
    overflow: hidden;
    background-color: var(--background-color);
}

/*  */
.progress::before {
    background: linear-gradient(
        45deg,
        var(--c2) 33%,
        var(--c1) 34%,
        var(--c1) 59%,
        var(--c2) 60%
    );
    background-size: calc(var(--height) * 2) var(--height);
    display: block;
    animation: rolling 60s linear infinite;
    transition: 0.3s cubic-bezier(0.5, 0.5, 0, 1.2);
}
@keyframes rolling {
    100% {
        background-position: calc(var(--height) * 400);
    }
}

/*  */

.progress.pause::before {
    animation-play-state: paused;
}

.progress::after,
.progress::before {
    width: var(--percentage);
}

/*  */
.progress.complete::after {
    background: var(--complete);
}
.progress.failed::after {
    background: var(--failed);
}

.circle {
    background: unset;
    transform: rotate(-90deg);
    display: inline-block;
    max-height: 120px;
    max-width: 120px;
    min-height: 1.5em;
    min-width: 1.5em;
}

.circle .background {
    stroke: var(--border);
    fill: none;
}

.circle .progressbar {
    /* stroke-dashoffset: var(--percentage-of-ring); */
    stroke-dasharray: 440;
    /* stroke-dashoffset: 220; */
    stroke-linecap: round;
    stroke: var(--info);
    fill: none;
}

.circle.complete .progressbar {
    stroke: var(--success);
}
.circle.failed .progressbar {
    stroke: var(--error);
}
.circle.pause .progressbar {
    stroke: var(--warning);
}
</style>

